import Vue from 'vue'
import Router from 'vue-router'

import AHome from '@/components/home'
import Login from '@/components/login'
import ANews from '@/components/news'
import AFiles from '@/components/files'
import APersons from '@/components/persons'
import AHeaders from '@/components/headers'
import AStudentlist from '@/components/studentlist'
import AVisiter from '@/components/visiter'
import AWorker from '@/components/worker'
import ASendnews from '@/components/sendnews'
import AVideos from '@/components/videos'
import AGoods from '@/components/goods'
import apartment from '@/components/apartment'


Vue.use(Router)

export default new Router({
  // mode:"history",
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    // {
    //   path: '/home',
    //   name: 'AHome',
    //   component: AHome
    // },
    {
      path: '/persons',
      name: 'APersons',
      component: APersons
    },{
      path: '/home',
      name: 'ANews',
      component: ANews
    },{
      path: '/files',
      name: 'AFiles',
      component: AFiles
    },{
      path: '/studentlist',
      name: 'AStudentlist',
      component: AStudentlist
    },{
      path: '/worker',
      name: 'AWorker',
      component: AWorker
    },{
      path: '/visiter',
      name: 'AVisiter',
      component: AVisiter
    },{
      path: '/sendnews',
      name: 'ASendnews',
      component: ASendnews
    },{
      path: '/videos',
      name: 'AVideos',
      component: AVideos
    },{
      path: '/goods',
      name: 'AGoods',
      component: AGoods
    },{
      path: '/apartment',
      name: 'apartment',
      component: apartment
    }
  ]
})

