<template>
  <div>
    <Headers @auther="showauther" ref="headercom"></Headers>
    <div class="sendnews">
<div class="container">
      <form class="form-signin"><br>
        <h2 class="form-signin-heading">新闻发布</h2><br>
        
    		  <router-link to="/home">
		  <p class="btn btn-normal float_right martopf">查看新闻</p>		  
		  </router-link>
 <div class="register_box">
  <div class="container">
   <div class="form-group">
    <label class="col-sm-2 control-label">标题</label>
    <div class="col-sm-10">
      <input type="text" class="form-control" ref="newTitle"  placeholder="请输入标题">
    </div>
  </div>

    <div class="form-group">
    <label class="col-sm-2 control-label">内容</label>
    <div class="col-sm-10">
        <textarea placeholder="请输入内容" ref="newContent" class="form-control" rows="5"></textarea>
    </div>
  </div>

     
  </div>
</div>

        <div class="rigbotBtns">
		    <!-- <button class="btn btn-default" type="resize">重置</button>     -->
            <button class="btn btn-primary" type="submit" @click="sendNews($event)">发布</button>        
        </div>      

      </form>

 

      <hr>

      <!-- <footer>
        <p>&copy; Company 2014</p>
      </footer> -->

    </div><!--/.container-->

    </div>
  </div>
</template>

<script>
import Headers from './headers'
import Axios from 'axios'
export default {
  name:'sendnews',
  data(){
    return{
      auther:'',
    }
  },
  mounted(){
    
  },
  methods:{
      sendNews(e){
        let _this = this
          e.preventDefault();
          this.$refs.headercom.pasauther()
          if(this.$refs.newTitle.value =="" || this.$refs.newContent.value ==""){
            _this.$message('标题和内容都不能为空')            
            return
          }else{
            Axios({
              url:'/VueHandler/AdminHandler?action=sendNews',
              method:'post',
              data:{
                  newTitle:_this.$refs.newTitle.value,
                  newContent:_this.$refs.newContent.value,
                  newAuther:_this.auther
              }
          }).then(function(res){
              console.log(res.data)
              if(res.data.success){
                  _this.$message("新闻发布成功")
              }
          });
          }
          
          this.$refs.newTitle.value = "",
          this.$refs.newContent.value = ""
      },
      showauther(user){
        this.auther=user
      },
      // getuser(){
      //   this.$refs.headercom.pasauther()
      // }

      
  },
  components:{Headers}
}
</script>

<style scoped>

</style>



