<template>
    <div>
        <Headers></Headers>
        <div class="video_list container">
            <ul class="video_ul">
                <li class="video_li" v-for="item in videoData" v-if="item.fileType=='video/mp4'">
                    <!-- <p class="video_title">{{'../..'+item.filePathAndName.substr(11,item.filePathAndName.length)}}</p> -->
                    <video :src="require('../assets/media/'+item.newfileName)" controls></video>
                    <div class="video_bootom">
                        <p class="video_title">上传者：<span>{{item.fileAuther}}</span></p> 
                        <p class="video_auther">{{item.fileName}}</p>
                    </div>
                </li>
            </ul>
        </div>
    </div>
</template>
<script>
import Headers from './headers'
import Axios from 'axios'
export default {
    data(){
        return{
            videoData:[],
        }
    },
    mounted(){
        this.getVideoList()
    },
    methods:{
        getVideoList(){
            var _this=this;
            Axios({
                url:'/VueHandler/AdminHandler?action=getFileList',
                method:'get',
                params:{

                }
            }).then(function(res){
                if(res.data.success){
                    _this.videoData = res.data.data.reverse();
                    console.log(_this.videoData)
                    // console.log(_this.videoData.filePathAndName)
                }
            })

        }
    },
    components:{
        Headers
    }
}
</script>



