<template>
  <div class="headers">
      <nav class="navbar navbar-expand-md navbar-dark bg-dark fixed-top">
  <a class="navbar-brand" href="#"><img src="../assets/images/logo.png" alt=""></a>
  <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarsExampleDefault" aria-controls="navbarsExampleDefault" aria-expanded="false" aria-label="Toggle navigation">
    <span class="navbar-toggler-icon"></span>
  </button>

  <div class="collapse navbar-collapse" id="navbarsExampleDefault">
    <ul class="navbar-nav mr-auto">
      <!-- <li class="nav-item">
        <a class="nav-link"><router-link to="/home">首页</router-link></a>                
      </li> -->
      <li class="nav-item">
        <a class="nav-link"><router-link to="/studentlist">学生管理</router-link></a>
      </li>
       <li class="nav-item">
        <a class="nav-link"><router-link to="/worker">员工管理</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/apartment">公寓管理</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/visiter">来客登记</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/goods">物品登记</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/home">新闻模块</router-link></a>        
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/sendnews">发布新闻</router-link></a>        
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/persons">管理员</router-link></a>
      </li>
      <!-- <li class="nav-item">
        <a class="nav-link"><router-link to="/files">文件</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="/videos">视频</router-link></a>
      </li> -->

      <!-- <li class="nav-item dropdown">
        <a class="nav-link dropdown-toggle" href="http://example.com" id="dropdown01" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">Dropdown</a>
        <div class="dropdown-menu" aria-labelledby="dropdown01">
          <a class="dropdown-item" href="#">Action</a>
          <a class="dropdown-item" href="#">Another action</a>
          <a class="dropdown-item" href="#">Something else here</a>
        </div>
      </li> -->
    </ul>
    <ul class="navbar-nav nav-right">
      <li class="nav-item">
        <a class="nav-link" @click="editPas()" data-toggle="modal" data-target="#myModalrepas"><router-link to="">安全中心</router-link></a>
      </li>
      <li class="nav-item">
        <a class="nav-link" >欢迎</a>
      </li>
      <li class="nav-item">
        <a class="nav-link"><router-link to="">{{user}}</router-link></a>
      </li>
      <li class="nav-item" @click="quit()">
        <a class="nav-link"><router-link to="" >退出</router-link></a>
      </li>
      <li class="nav-item" v-if="!user">
        <a class="nav-link"><router-link to="/register">注册</router-link></a>
      </li>
      <li class="nav-item" v-if="!user">
        <a class="nav-link"><router-link to="/">登录</router-link></a>
      </li>
    </ul>
    <RePas></RePas>
  </div>
</nav>


  </div>
</template>

<script>
import Axios from 'axios'
import RePas from './rePassword'
import storage from '../assets/js/storage'
export default {
    name:'Headers',
    data(){
        return{
            user:'',
            _id:''
        }
    },
    components:{
      RePas
    },
    mounted(){
      this.getUserName()
      this.pasauther()
      this.getmyInfos()
      this.getmyusername()
    },
    methods:{
      getUserName(){
        var _this=this;
        Axios({
          url:'/VueHandler/AdminLoginAndRegHandler?action=returnuserinfo',
          method:'post'
        }).then(function(res){
          _this.user=res.data.data.turename
          _this._id=res.data.data._id
        })
      },
      quit(){
        let _this = this
          Axios({
            url:'/VueHandler/AdminHandler?action=quit',
            method:'get'
          }).then(function(res){
            if(res.data.success){
              storage.delSession("userInfo")
              _this.$message.success('退出成功');
              window.location='/'
            }
          })
      },
      editPas(){

      },
      pasauther(){
        this.$emit("auther",this.user)
        // console.log(this.user)
      },
      getmyInfos(){
        this.$emit("getmyInfo",[this.user,this._id])
      },
      getmyusername(){
        this.$emit("getmyusername",this.user)
      }
    }
};
</script>

<style scoped>
  .navbar-brand img{
        width: 58px;
    height: 58px;
    position: absolute;
    top: -2px;

  }
  .navbar-brand{
    width: 60px;
  }
</style>



