<template>
    <div class="addAdmin">
<div class="modal fade" id="myModalrepas" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-hidden="true">×</button>
                <h4 class="modal-title" id="myModalLabel">修改密码</h4>
            </div>
            <div class="modal-body">
                <div class="container">
  <div class="form-group">
    <label class="col-sm-3 control-label">旧密码</label>
    <div class="col-sm-9">
      <input type="text" class="form-control" ref="oldpas" placeholder="请输入旧密码">
    </div>
  </div>
    <div class="form-group">
    <label class="col-sm-3 control-label">新密码</label>
    <div class="col-sm-9">
      <input type="text" class="form-control" ref="newpas" placeholder="请输入新密码">
    </div>
  </div>
    <div class="form-group">
    <label class="col-sm-3 control-label">确认密码</label>
    <div class="col-sm-9">
      <input type="text" class="form-control" ref="rnewpas" placeholder="请再次输入新密码">
    </div>
  </div>
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                <button type="button" class="btn btn-primary"  @click="repasswords()">确认</button>
            </div>
        </div><!-- /.modal-content -->
    </div><!-- /.modal-dialog -->
</div>
    </div>
</template>
<script>
import Axios from 'axios'
export default {
  name: "rePassword",
  data() {
    return {
        
    };
  },
  methods: {
      repasswords(){
          var _this=this;
          Axios({
              url:'/VueHandler/AdminHandler?action=updatepass',
              method:'post',
              data:{
                  userPwd:_this.$refs.oldpas.value,
                  newPwd:_this.$refs.newpas.value
              }
          }).then(function(res){
              if(_this.$refs.newpas.value==_this.$refs.rnewpas.value){
              if(res.data.success){
                  alert("密码修改成功");
              }else{
                  alert("密码修改失败");
              }
              }else{
                  alert("两次输入不一致，请重新输入");
              }

          })
      }
  }
};
</script>




