<template>
  <div>
    <Headers @getmyInfo="userandid" ref="headcom"></Headers>
    <!-- <Chat ref="chatchild" :turenameres="turenameres" :turenamereq="turenamereq"></Chat> -->
    <div class="Tpersons">
      <div class="container"><br>
      <div class="row Tsearch_box">
        <div class="col-md-12 row">
          <el-input type="text"  v-model="searchuser" class="col-md-2" placeholder="请输入姓名"></el-input>
          <el-input type="text"  v-model="searchhome" class="col-md-2" placeholder="请输入宿舍"></el-input>
          <el-input type="text"  v-model="searchphone" class="col-md-2" placeholder="请输入电话号"></el-input>
          <!-- <el-input type="text"  v-model="searchsnum" class="col-md-2" placeholder="请输入日期"></el-input> -->
          <el-date-picker v-model="searchdate" type="date" class="col-md-2" value-format="yyyy-MM-dd" placeholder="来访时间搜索"></el-date-picker>
          <el-button type="primary" @click="search()">查询</el-button>
          <el-button type="success" @click="addStudent()">添加</el-button> 
        </div>
    <div class="table-responsive"><br>
        <h6>访客列表</h6>     
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>姓名</th>
            <th>性别</th>            
            <th>年龄</th>
            <th>手机号</th>
            <th>访问宿舍</th>
            <th>与访客关系</th>
            <th>来访时间</th>
            <th>结束时间</th>
            <th style="width:160px">操作</th>            
          </tr>
        </thead>
          <tbody>
    <tr v-for="tableData in tableDatas">
      <td>{{tableData.userName}}</td>
      <td>{{tableData.sex | sexFilter}}</td> 
      <td>{{tableData.old}}</td>
      <td>{{tableData.phone}}</td>
      <td>{{tableData.homeNum}}</td>
      <td>{{tableData.rela | relaFilster}}</td>   
      <td>{{tableData.createAt}}</td>
      <td v-if="tableData.isCompent=='1'">{{tableData.updateAt}}</td>
      <td style="color:red" v-else>正在访问中。。。</td>
      <td>
        <template>
          <button class="btn btn-success btn-sm" v-if="tableData.isCompent=='0'" @click="compentRow(tableData)">结束访问</button>
          <button class="btn btn-danger btn-sm"  @click="delRow(tableData)">删除</button>
        </template>  
      </td>
    </tr>
  </tbody>
      </table>
    </div>
    <div class="table_bottom">
            <!-- <div>共 {{totles}} 条数据</div> -->
			<div class="pages">
				<el-pagination @current-change="pageStarts" :current-page.sync="currentPage1" :page-size="6" layout="total, prev, pager, next" :total="totles">
				</el-pagination>
			</div>
    </div>
  </div>
  </div>

    </div>
    <div class="add_box">
      <el-dialog
        title="访客信息"
        :visible.sync="dialogVisible"
        width="60%">
          <el-form :model="form" label-width="80px" size="mini" :rules="rules" ref="ruleForm">
            <el-form-item label="姓名" prop="name">
              <el-input v-model="form.name" ></el-input>
            </el-form-item>
            <el-form-item label=性别 prop="sex">
              <el-radio-group v-model="form.sex">
                <el-radio label="1">男</el-radio>
                <el-radio label="0">女</el-radio>
              </el-radio-group>
            </el-form-item>
            <el-form-item label="年龄" prop="old">
              <el-input v-model="form.old" type="number"></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phone">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
            <el-form-item label="访问宿舍">
              <el-input v-model="form.homeNum" ></el-input>              
            </el-form-item>
            <el-form-item label="关系" prop="rela">
              <el-select v-model="form.rela">
                <el-option value="1" label="亲人"></el-option>
                <el-option value="2" label="朋友"></el-option>
                <el-option value="3" label="同学"></el-option>
                <el-option value="4" label="其他"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="sumbitForm()">确定</el-button>
        </span>
      </el-dialog>

    </div>
  </div>
</template>

<script>
import Headers from "./headers";
// import Chat from "./chat"
import Axios from "axios";
import { store } from "../vuex/store.js";
import Element from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import Vue from "vue";
Vue.use(Element);
export default {
  store,
  name: "news",
  data() {
    return {
      searchuser: "",
      searchsnum: "",
      searchhome:"",
      searchphone:"",
      searchdate:"",
      pageStart: 1,
      totles: 1,
      tableDatas: [],
      currentPage1: 1,
      _idreq:'',
      _idres:'',
      turenamereq:'',
      turenameres:'',
      dialogVisible:false,
      form:{
        
      },
      rules:{
        // name:[
        //   { required: true, message: '请输入姓名', trigger: 'blur' },
        // ]
      }
    };
  },
  filters:{
    sexFilter(val){
      if(val=='0'){
        return "女"
      }else{
        return "男"
      }
    },
    buildNumFilter(val){
      if(val=='1'){
        return '一号楼'
      }else if(val=='2'){
        return '二号楼'        
      }else if(val=='3'){
        return '三号楼'        
      }else if(val=='4'){
        return '四号楼'        
      }else if(val=='5'){
        return '五号楼'        
      }else if(val=='6'){
        return '六号楼'        
      }
    },
    relaFilster(val){
      if(val == '1'){
        return "亲人"
      }else if(val == '2'){
        return "朋友"
      }else if(val == '3'){
        return "同学"
      }else if(val == '4'){
        return "其他"
      }
    },
    // changeDate(val){
    //   return new Date(val).toLocaleDateString()+' '+new Date(val).toLocaleTimeString()
    // }
  },
  methods: {
    getStudentList() {
      var _this = this;
      console.log(_this.searchdate);
      
      Axios({
        url: "/VueHandler/AdminHandler?action=visitshow",
        method: "post",
        data: {
          pageStart: _this.pageStart,
          username: _this.searchuser,
          number: _this.searchsnum,
          homeNum: _this.searchhome,
          phone: _this.searchphone,
          date: _this.searchdate
        }
        
      }).then(function(res) {
        // console.log(new Date(_this.searchdate).toString().slice(0,-27));
        
        if (res.data.success) {
          _this.tableDatas = res.data.data.list;
          _this.totles = res.data.data.count;
          // console.log(_this.tableDatas)
        } else {
          _this.$message("没有数据");
        }
      });
    },
    //分页查询
    pageStarts(val) {
      //console.log(`当前页: ${val}`);
      this.pageStart = val;
      this.getStudentList(this.pageStart);
    },
    search() {
      this.getStudentList();
    },
    addStudent() {
      this.form={
        sex:'1',
      }
      this.dialogVisible = true
    },
    userandid(user){
      this.turenamereq=user[0],
      this._idreq=user[1]
      // console.log(this._idreq,this.turenamereq)
    },
    sumbitForm(){
      let _this = this
      let datas = {
            username:this.form.name,
            sex:this.form.sex,
            old:this.form.old,       
            phone:this.form.phone,       
            homeNum:this.form.homeNum,       
            rela:this.form.rela,       
          }
      if(this.form.tokenId){
        
      }else{
        Axios({
          url:'/VueHandler/AdminHandler?action=addvisit',
          method:'post',
          data:datas
        }).then(function(res){
          if(res.data.success){
            _this.$message("添加成功");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("添加失败");          
            return
          }
        })
      }
    },
    compentRow(row){
      console.log(row);
      let _this = this
      this.$confirm('是否确定访问结束?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          Axios({
          url:'/VueHandler/AdminHandler?action=editvisit',
          method:'post',
          data:{
            tokenId:row.tokenId,
            isCompent:'1'
          }
        }).then(function(res){
          if(res.data.success){
            _this.$message("访问结束完成");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("访问结束失败");          
            return
          }
        })
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });  
        
    },
    delRow(row){
      console.log(row.tokenId);
      let _this = this
      this.$confirm('是否确定删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          Axios({
            url:'/VueHandler/AdminHandler?action=delvisit',
            method:'get',
            params:{
              tokenId:row.tokenId
            }
          }).then(function(res){
            console.log(res.data)
            if(res.data.success){
              _this.getStudentList()
              _this.$message("删除成功")
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });      
    }
  },
  components: { Headers },
  computed: {
    studentList() {
      return this.$store.state.studentList;
    }
  },
  mounted() {
    this.getStudentList();
  }
};
</script>

<style scoped>
.form-control{
  margin: 0 0px;
}
.el-date-editor.el-input{
  padding: 0 5px;
}
</style>



