<template>
  <div>
    <Headers @getmyusername="myusername" ref="headerscom"></Headers>
    <div class="file container">
   <!-- accept="image/*" -->
   <div class="file_top row">
     <div class="col-md-6">
        <input type="file" name="file" id="file" ref="upload" style="display:none" @change="add_file">
        <label class="btn btn-primary" for="file">选择文件</label>
        <span class="selectword">已选择文件：</span><span>{{fileName}}</span>
     </div>
      <!-- <div class="col-sm-4">
        <input type="text" class="form-control" ref="fileScribe"  placeholder="描述">
      </div> -->
        <div class="col-sm-6">
        <span  class="btn btn-success btn_push" @click="pushFiles($event)">上传</span>        
        </div>     
   </div>
   <div class="filetable">
     <table class="table">
       <tr>
         <th>文件名</th>
         <th>大小(M)</th>
         <!-- <th>类型</th> -->
         <th>上传者</th>
         <th>上传时间</th>         
         <th>操作</th>           
       </tr>
       <tr v-for="item in fileData">
         <td>{{item.fileName}}</td>
         <td>{{item.fileSize/1024}}</td>
         <!-- <td>{{item.fileType}}</td> -->
         <td>{{item.fileAuther}}</td>
         <td>{{item.createAt}}</td>         
         <td>
           <!-- {{item.filePathAndName}} -->
           <button class="btn btn-success btn-sm" @click="downLoanFile(item)">下载</button>
          <button class="btn btn-danger btn-sm" @click="deleteFile(item)">删除</button>
         </td>
                  
       </tr>
     </table>
   </div>
    </div>
  </div>
</template>
<script>
import Axios from 'axios'
import Headers from './headers'
export default {
  name:'files',
  data(){
    return{
    stock_arr:[],
    cacheName:'',
    fileName:'',
    fileAuther:'',
    fileData:'',
    filePathAndName:'',
    newfileName:'',
    downFilePath:''
  }
  },
  mounted(){
    this.getFileList()
    // this.myusername()
  },
  methods:{
    add_file(){
      if(!this.$refs.upload.files[0]){
        return
      }
      this.fileName=this.$refs.upload.files[0].name;
      var _this=this
      var reader = new FileReader()
      reader.readAsDataURL(this.$refs.upload.files[0])
      reader.onload=function(e){
        _this.stock_arr.push({
          img:e.target.result,
          cacheName:_this.cacheName,
          file:_this.$refs.upload.files[0]
        })
      }
      
    },
    pushFiles(){
      if(this.$refs.upload.value.length!=0){
              var _this=this
      var formData=new FormData();
      formData.append('file',this.$refs.upload.files[0])
      Axios({
        url:'/VueHandler/UpLoadVideoHandler?action=addf',
        method:'post',
        anync: true,
        contentType: false,
        processData: false,
        data: formData,
        
      }).then(function(res){
          _this.filePathAndName=res.data.cacheName.split("\\").join("/");
          
          _this.downFilePath=_this.filePathAndName.slice(0,19)
              _this.newfileName=_this.filePathAndName.substr(25,_this.filePathAndName.length)
              console.log(_this.newfileName)
          
        if(res.data.success){
          alert('上传成功')
          _this.$refs.headerscom.getmyusername()
          // console.log(_this.$refs.upload.files[0].type)
          Axios({
            url:'/VueHandler/AdminHandler?action=uploadfileinfo',
            method:'post',
            data:{
              // fileScribe:_this.$refs.fileScribe.value,
              fileName:_this.$refs.upload.files[0].name,
              fileSize:_this.$refs.upload.files[0].size,
              fileType:_this.$refs.upload.files[0].type,
              fileAuther:_this.fileAuther,
              filePathAndName:_this.filePathAndName,
              newfileName:_this.newfileName,
            }
          }).then(function(res){
            if(res.data.success){
              _this.getFileList()

            }
          })
        }
      })
      }else{
        alert("请先选择文件")
      }


    },
    getFileList(){
      var _this=this;
      Axios({
        url:'/VueHandler/AdminHandler?action=getFileList',
        method:'get',
        params:{

        }
      }).then(function(res){
        if(res.data.success){
          _this.fileData=res.data.data.reverse()
          // console.log(_this.fileData)
        }
      })
    },
    myusername(user){
      this.fileAuther=user
      // console.log(this.fileAuther)
    },
    downLoanFile(item){
      var _this=this;
      console.log('../HBKJ/src/assets/downfiles/'+item.fileName)
      console.log(item.filePathAndName)
      Axios({
        url:'/VueHandler/AdminHandler?action=downLoadFile',
        method:'get',
        params:{
          downFilePath:'C:/Users/Administrator/Downloads/'+item.fileName,
          filePathAndName:item.filePathAndName
        }
      }).then(function(res){
        if(res.data.success){
          alert("已下载到本地")
        }else{
          alert("下载失败")
        }
      })
      alert("已下载到本地")
    },
    deleteFile(item){
      var _this=this;
      // console.log(item.ID)
      Axios({
        url:'/VueHandler/AdminHandler?action=deleteFile',
        method:'get',
        params:{
          newfileName:_this.newfileName,
          filePathAndName:item.filePathAndName
        }
      }).then(function(res){
        // console.log(res.data)
        if(res.data.success){
          Axios({
            url:'/VueHandler/AdminHandler?action=deleteFileInfo',
            method:'get',
            params:{
              ID:item.ID
            }
          }).then(function(res){
            console.log(res)
            if(res.data.success){
              alert('删除成功')
              _this.getFileList()
            }
          })
        }
      })
    }
  },
  components:{Headers}
}
</script>

<style scoped>

</style>



