<template>
  <div>
    <Headers @getmyInfo="userandid" ref="headcom"></Headers>
    <!-- <Chat ref="chatchild" :turenameres="turenameres" :turenamereq="turenamereq"></Chat> -->
    <div class="Tpersons">
      <div class="container"><br>
      <div class="row Tsearch_box">
        <div class="col-md-12 row">
          <el-input type="text"  v-model="searchsnum" class="col-md-2" placeholder="请输入房间号"></el-input>
          <el-input type="text"  v-model="searchphone" class="col-md-2" placeholder="请输入评分"></el-input>
          <el-button type="primary" @click="search()">查询</el-button>
          <el-button type="success" @click="addStudent()">添加</el-button> 
        </div>
    <div class="table-responsive"><br>
        <h6>公寓列表</h6>     
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>房间号</th>
            <th>板凳</th>            
            <th>桌子</th>
            <th>风扇</th>
            <th>饮水机</th>
            <th>水费(元)</th>
            <th>电费(元)</th>
            <th>网费(元)</th>
            <th>评分</th>
            <th style="width:140px">操作</th>            
          </tr>
        </thead>
          <tbody>
    <tr v-for="tableData in tableDatas">
      <td>{{tableData.number}}</td>
      <td>{{tableData.bandeng}}</td> 
      <td>{{tableData.zhuozi}}</td>
      <td>{{tableData.fengshan}}</td>
      <td>{{tableData.yinshuiji}}</td>
      <td>{{tableData.shuifei}}</td>
      <td>{{tableData.dianfei}}</td>
      <td>{{tableData.wangfei}}</td>
      <td>{{tableData.pingfeng}}</td>
      <td>
        <template>
          <button class="btn btn-success btn-sm"  @click="editRow(tableData)">编辑</button>
          <button class="btn btn-danger btn-sm"  @click="delRow(tableData)">删除</button>
        </template>  
      </td>
    </tr>
  </tbody>
      </table>
    </div>
    <div class="table_bottom">
            <!-- <div>共 {{totles}} 条数据</div> -->
			<div class="pages">
				<el-pagination @current-change="pageStarts" :current-page.sync="currentPage1" :page-size="6" layout="total, prev, pager, next" :total="totles">
				</el-pagination>
			</div>
    </div>
  </div>
  </div>

    </div>
    <div class="add_box">
      <el-dialog
        title="公寓信息"
        :visible.sync="dialogVisible"
        width="45%">
          <el-form :model="form" label-width="80px" size="mini" :rules="rules" ref="ruleForm">
            <el-form-item label="房间号" prop="number">
              <el-input v-model="form.number" ></el-input>
            </el-form-item>
            <el-form-item label=板凳 prop="bandeng">
              <el-input v-model="form.bandeng" type="number"></el-input>              
            </el-form-item>
            <el-form-item label="桌子" prop="zhuozi">
              <el-input v-model="form.zhuozi" type="number"></el-input>
            </el-form-item>
            <el-form-item label="风扇" prop="fengshan">
              <el-input v-model="form.fengshan" type="number"></el-input>
            </el-form-item>
            <el-form-item label="饮水机" prop="yinshuiji">
              <el-input v-model="form.yinshuiji" type="number"></el-input>
            </el-form-item>
            <el-form-item label="水费" prop="shuifei">
              <el-input v-model="form.shuifei" type="number"></el-input>
            </el-form-item>
            <el-form-item label="电费" prop="dianfei">
              <el-input v-model="form.dianfei" type="number"></el-input>
            </el-form-item>
            <el-form-item label="网费" prop="wangfei">
              <el-input v-model="form.wangfei" type="number"></el-input>
            </el-form-item>
            <el-form-item label="评分" prop="pingfeng">
              <el-input v-model="form.pingfeng" type="number"></el-input>
            </el-form-item>
            
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="sumbitForm()">确定</el-button>
        </span>
      </el-dialog>

    </div>
  </div>
</template>

<script>
import Headers from "./headers";
// import Chat from "./chat"
import Axios from "axios";
import { store } from "../vuex/store.js";
import Element from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
import Vue from "vue";
Vue.use(Element);
export default {
  store,
  name: "news",
  data() {
    return {
      searchsnum: "",
      searchphone:"",
      pageStart: 1,
      totles: 1,
      tableDatas: [],
      currentPage1: 1,
      _idreq:'',
      _idres:'',
      turenamereq:'',
      turenameres:'',
      dialogVisible:false,
      form:{
        
      },
      rules:{
        // name:[
        //   { required: true, message: '请输入姓名', trigger: 'blur' },
        // ]
      }
    };
  },
  filters:{
    sexFilter(val){
      if(val=='0'){
        return "女"
      }else{
        return "男"
      }
    },
    
  },
  methods: {
    getStudentList() {
      var _this = this;
      Axios({
        url: "/VueHandler/AdminHandler?action=apartshow",
        method: "post",
        data: {
          pageStart: _this.pageStart,
          number: _this.searchsnum,
          phone: _this.searchphone,
        }
      }).then(function(res) {
        if (res.data.success) {
          _this.tableDatas = res.data.data.list;
          _this.totles = res.data.data.count;
          // console.log(_this.tableDatas)
        } else {
          _this.$message("没有数据");
        }
      });
    },
    //分页查询
    pageStarts(val) {
      //console.log(`当前页: ${val}`);
      this.pageStart = val;
      this.getStudentList(this.pageStart);
    },
    search() {
      this.getStudentList();
    },
    addStudent() {
      this.form={
        sex:'1',
        buildNum:'1'
      }
      this.dialogVisible = true
    },
    userandid(user){
      this.turenamereq=user[0],
      this._idreq=user[1]
      // console.log(this._idreq,this.turenamereq)
    },
    sumbitForm(){
      let _this = this
      let datas = {
            number:this.form.number,
            bandeng:this.form.bandeng,
            zhuozi:this.form.zhuozi,       
            yinshuiji:this.form.yinshuiji,       
            fengshan:this.form.fengshan,       
            shuifei:this.form.shuifei,       
            dianfei:this.form.dianfei,       
            wangfei:this.form.wangfei,       
            pingfeng:this.form.pingfeng,       
          }
      if(this.form.tokenId){
        datas.tokenId=this.form.tokenId
        Axios({
          url:'/VueHandler/AdminHandler?action=editapart',
          method:'post',
          data:datas
        }).then(function(res){
          if(res.data.success){
            _this.$message("修改成功");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("修改失败");          
            return
          }
        })
      }else{
        Axios({
          url:'/VueHandler/AdminHandler?action=addapart',
          method:'post',
          data:datas
        }).then(function(res){
          if(res.data.success){
            _this.$message("添加成功");
            _this.getStudentList()                    
            _this.dialogVisible = false
          }else{
            _this.$message("添加失败");          
            return
          }
        })
      }
    },
    editRow(row){
      console.log(row);
      this.dialogVisible = true
      this.form = Object.assign({},this.form,{
        number : row.number,
        tokenId : row.tokenId,
        bandeng : row.bandeng,
        zhuozi : row.zhuozi,
        fengshan : row.fengshan,
        yinshuiji : row.yinshuiji,
        shuifei : row.shuifei,
        dianfei : row.dianfei,
        wangfei : row.wangfei,
        pingfeng : row.pingfeng,
      })
    },
    delRow(row){
      console.log(row.tokenId);
      let _this = this
      this.$confirm('是否确定删除?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          Axios({
            url:'/VueHandler/AdminHandler?action=delapart',
            method:'get',
            params:{
              tokenId:row.tokenId
            }
          }).then(function(res){
            console.log(res.data)
            if(res.data.success){
              _this.getStudentList()
              _this.$message("删除成功")
            }
          })

        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          });          
        });      
    }
  },
  components: { Headers },
  computed: {
    studentList() {
      return this.$store.state.studentList;
    }
  },
  mounted() {
    this.getStudentList();
  }
};
</script>

<style scoped>
.form-control{
  margin: 0 0px;
}
</style>



