<template>
	<div>
		<canvas id="canvas" @click="changeVer" width="100%" height="34"></canvas>
	</div>
</template>

<script>
	import Axios from 'axios'
	export default{
		data(){
			return{
//				获取当前canvas元素
				canvas:"",
//				获取当前canvas的宽
				width:"",
//				获取当前canvas的高
				height:"",
//				当前canvas的绘制环境
				ctx:""
			}
		},
		mounted(){
			//获取元素节点id
			this.canvas=document.getElementById("canvas");
//			console.log(this.canvas)
			//设置宽高
			this.width=this.canvas.width;
			this.height=this.canvas.height;
			//绘制2d环境
			this.ctx=this.canvas.getContext("2d");
			this.getver(this.drawPic);
		},
		methods:{
			//请求验证码
			getver(fn){
				Axios({
					url:"/VueHandler/AdminLoginAndRegHandler?action=verification",
					method:"get",
				}).then(function(res){
					fn(res.data.data)
				})
			},
//			设置随机数
			randomNum(min,max){
				return Math.floor(Math.random()*(max-min)+min)
			},
			//产生随机颜色
			randomColor(min,max){
				var r=this.randomNum(min,max);
				var g=this.randomNum(min,max);
				var b=this.randomNum(min,max);
				return 'rgb('+r+','+g+','+b+')';
			},
			//绘制文字
			drawPic(returnTxt){
				//绘制背景色
				this.ctx.fillStyle=this.randomColor(120,250);
				//绘制矩形
				this.ctx.fillRect(0,0,this.width,this.height);
				//设置文字
				for(var i=0;i<4;i++){
					var txt=returnTxt[i];
					this.ctx.fillStyle=this.randomColor(0,120);
					this.ctx.font=this.randomNum(18,22)+"px Arial";
					var x=8*i+10;
					var y=this.randomNum(10,13);
					var deg=this.randomNum(-10,10);
					//设置旋转角度
					this.ctx.translate(x,y);
					this.ctx.rotate(deg*Math.PI/180);
					this.ctx.fillText(txt,x,y);
					//初始化
					this.ctx.rotate(-deg*Math.PI/180);
					this.ctx.translate(-x,-y);
				};
				//绘制干扰线
				for (var i=0;i<4;i++) {
					this.ctx.strokeStyle=this.randomColor(100,200);
					//开启画笔
					this.ctx.beginPath();
					//绘制线条的起始点
					// this.ctx.moveTo(this.randomNum(0,100),this.randomNum(0,34));
					//绘制链接线
					// this.ctx.lineTo(this.randomNum(0,100),this.randomNum(0,34));
					//关闭画笔
					this.ctx.closePath();
					this.ctx.stroke();
				};
				//绘制小点
				for (var i=0;i<7;i++) {
					this.ctx.fillStyle=this.randomColor(0,255);
					this.ctx.beginPath();
					this.ctx.arc(this.randomNum(0,100),this.randomNum(0,100),2,0,2*Math.PI);
					this.ctx.closePath();
					this.ctx.fill();
				}
			},
			changeVer(){
				this.getver(this.drawPic)
			}
		}
	}
</script>
<!--scoped当前作用页-->
<style scoped="scoped">
	#canvas{
		float: right;
		cursor: pointer;
		
	}
</style>


