<template>
  <div class="home">
    <Headers></Headers> 
    <div class="home_box">
    <div class="lunbo">
      <div class="swiper-container">
        <div class="swiper-wrapper">
          <div class="swiper-slide"><img src="../assets/images/slider-2.jpg"></div>
          <div class="swiper-slide"><img src="../assets/images/slider-3.jpg"></div>
          <div class="swiper-slide"><img src="../assets/images/slider-4.jpg"></div>
        </div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import Headers from "./headers";
import Swiper from "swiper";
export default {
  name: "Home",
  data() {
    return {
     
    };
  },
  mounted(){
      this.swfun();
	},
  components: {
    Headers
	},
  methods:{
          swfun(){
            new Swiper(".swiper-container", {
              pagination: ".swiper-pagination",
              paginationClickable: true,
              speed: 2000,
              loop: true,
              observer: true,
              observeParents: true,
              autoplayDisableOnInteraction: true,
              autoplay: 1500
          });
       },
       senNavfun(item){
         for(var i=0;i<this.navData.length;i++){
           this.navData[i].show=false;
         }
         item.show=true;
       }
    
    },

    
};
</script>

<style scoped>
</style>



