<template>
  <div>
    <Headers></Headers>
    <div class="news">
        <div class="container">

      <div class="row row-offcanvas row-offcanvas-right">

        <div class="col-xs-12 col-sm-12">
          <br>
          <div class="jumbotron">
            <h2>{{pullTopData.newTitle}}</h2>
            <p class="newContent">{{pullTopData.newContent}}</p>
            <p class="new_time">发布者 ：  {{pullTopData.Auther}}</p>
            <p class="new_time">发布时间 ：  {{pullTopData.createAt}}</p>  
          </div>
          <div class="row">
            <div class="col-xs-6 col-lg-4" v-for="item in newsList">
              <h4>{{item.newTitle}}</h4>
              <p class="newContent">{{item.newContent}}</p>
              <div class="textrig">
                <a class="btn btn-primary btn-sm btn_zd" href="" @click="pullTop($event, item)" role="button">置顶</a>
               <p class="btn btn-success btn-sm" @click="editNew(item)" data-toggle="modal" data-target="#editNew">修改</p>
               <p class="btn btn-danger btn-sm" @click="deleNew(item)">删除</p>
              </div>
             
              <p class="new_time">发布者 ：  {{item.Auther}}</p>
              <p class="new_time">发布时间 ：  {{item.createAt}}</p>
                           
              <!-- <p class="new_time">发布者 ： {{}}</p>   -->
            </div><!--/.col-xs-6.col-lg-4-->
            
          </div><!--/row-->
        </div><!--/.col-xs-12.col-sm-9-->

      </div><!--/row-->

      <!-- 修改新闻框 -->
      <div class="modal fade addAdmin" id="editNew" tabindex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
    <div class="modal-dialog">
        <div class="modal-content">
            <div class="modal-header">
                <button type="button" class="close" data-dismiss="modal" aria-hidden="true">×</button>
                <h4 class="modal-title" id="myModalLabel">修改新闻</h4>
            </div>
            <div class="modal-body">
                <div class="container">
                    <div class="form-group">

  </div>
  <div class="form-group">
    <label class="col-sm-3 control-label">标题</label>
    <div class="col-sm-9">
      <input type="text" class="form-control" ref="mnewtitle" placeholder="请输入标题">
    </div>
  </div>
    <div class="form-group">
    <label class="col-sm-3 control-label">内容</label>
    <div class="col-sm-9">
      <textarea placeholder="请输入内容" ref="mnewContent" class="form-control" rows="5"></textarea>
    </div>
  </div>
    <div style="display:none" ref="ID"></div>
    
                </div>
            </div>
            <div class="modal-footer">
                <button type="button" class="btn btn-default" data-dismiss="modal">关闭</button>
                <button type="button" class="btn btn-primary"  @click="editNewconf()">确认</button>
            </div>
        </div><!-- /.modal-content -->
    </div><!-- /.modal-dialog -->
</div>

      <hr>

      <footer>
        <p>&copy; Company 2014</p>
      </footer>

    </div><!--/.container-->

    </div>
  </div>
</template>

<script>
import Headers from './headers'
import Axios from 'axios'
export default {
  name:'news',
  data(){
    return{
      newsList:[],
      pullTopData:[]
    }
  },
  mounted(){
    this.getLists()
  },
  methods:{
    getLists(){
      var _this=this;
      Axios({
        url:'/VueHandler/AdminHandler?action=getNewList',
        method:'get',
        params:{

        }
      }).then(function(res){
        if(res.data.success){
          _this.newsList=res.data.data.reverse()
          _this.pullTopData=_this.newsList[0]
          console.log(_this.newsList)
        }
      })
    },
    pullTop(e,item){
      e.preventDefault();
      // console.log(item.newTitle)
      this.pullTopData=item
    },
    deleNew(item){
      Axios({
        url:'/VueHandler/AdminHandler?action=delNews',
        method:'get',
        params:{
          ID:item.ID
        }
      }).then(function(res){
        console.log(res.data)
      })
      this.getLists()
    },
    editNew(item){
      this.$refs.ID.value=item.ID,      
      this.$refs.mnewtitle.value=item.newTitle,
      this.$refs.mnewContent.value=item.newContent
    },
    editNewconf(){
      var _this=this;
      Axios({
        url:'/VueHandler/AdminHandler?action=editNews',
        method:'post',
        data:{
          ID:_this.$refs.ID.value,
          newTitle:_this.$refs.mnewtitle.value,
          newContent:_this.$refs.mnewContent.value
        }
      }).then(function(res){
        if(res.data.success){
          _this.$message("修改成功");
          _this.getLists()      
        }else{
          _this.$message("修改失败")
        }
      })
    }
  },
  components:{Headers}
}
</script>

<style scoped>

</style>



