export default{
    setSession(key, val){
        sessionStorage.removeItem(key)
        sessionStorage.setItem(key, JSON.stringify(val))
    },
    getSession(key){
        let data = sessionStorage.getItem(key)
        if(data != null){
            return JSON.parse(data)
        }else{
            return data
        }
    },
    delSession(key){
        sessionStorage.removeItem(key)
    }
}


