<template>
  <div>
    <Headers @getmyInfo="userandid" ref="headcom"></Headers>
    <!-- <Chat ref="chatchild" :turenameres="turenameres" :turenamereq="turenamereq"></Chat> -->
    <div class="Tpersons">
      <!-- <div class="Tstore">{{studentList}}</div> -->
      <div class="container"><br>
      <div class="row Tsearch_box">
          <input type="text"  v-model="search_input" class="form-control col-md-8" placeholder="请输入真实姓名">
          <div class="col-md-4">
            <div class="btn btn-primary col-md-4 col-sm-12" @click="search()">查询</div>
            <div class="btn btn-success col-md-4 col-sm-12"  data-toggle="modal" data-target="#myModal" @click="add()">添加</div> 
          </div>
    <div class="table-responsive"><br>
        <h6>管理员列表</h6>      
      <table class="table table-bordered">
        <thead>
          <tr>
            <th>用户名</th>
            <th>姓名</th>
            <th>后台权限</th>
            <th>手机号</th>
            <th>编辑日期</th>
            <th>操作</th>            
          </tr>
        </thead>
          <tbody>
    <tr v-for="tableData in tableData">
      <td>{{tableData.userName}}</td>
      <td>{{tableData.turename}}</td>
      <td>{{tableData.power}}</td>
      <td>{{tableData.phone}}</td>
      <td>{{tableData.upDateAt}}</td>
      <td>
        <!-- {{tableData.tokenId}} -->
        <template>
          <!-- <button class="btn btn-success btn-sm"  @click="chatShow(tableData)" data-toggle="modal" data-target="#charModal">聊天</button>           -->
          <button class="btn btn-primary btn-sm"  @click="editAdmins(tableData)" data-toggle="modal" data-target="#myModaledit">编辑</button>
          <button class="btn btn-danger btn-sm" @click="delAdmin(tableData)">删除</button>              
        </template>  
      </td>
    </tr>
  </tbody>
      </table>
    </div>
    <div class="table_bottom">
            <!-- <div>共 {{totles}} 条数据</div> -->
			<div class="pages">
				<el-pagination @current-change="pageStarts" :current-page.sync="currentPage1" :page-size="3" layout="total, prev, pager, next" :total="totles">
				</el-pagination>
			</div>
    </div>
      </div>
      </div>
      <!-- <AddAdmin @childMethod="getAdminList"></AddAdmin>  
      <EditAdmin ref="editmodle" @getlistmethod="getAdminList"></EditAdmin> -->
      <div class="add_box">
      <el-dialog
        title="添加管理员"
        :visible.sync="dialogVisible"
        width="60%">
          <el-form :model="form" label-width="80px" size="mini" :rules="rules" ref="ruleForm">
            <el-form-item v-if="!isEdit" label="用户名" prop="userName">
              <el-input v-model="form.userName" ></el-input>
            </el-form-item>
            <el-form-item label="姓名" prop="turename">
              <el-input v-model="form.turename" ></el-input>
            </el-form-item>
            <el-form-item label="手机号" prop="phone">
              <el-input v-model="form.phone"></el-input>
            </el-form-item>
            <el-form-item label="密码">
              <el-input v-model="form.password" type="password"></el-input>              
            </el-form-item>
            <el-form-item label="权限" prop="power">
              <el-select v-model="form.power">
                <el-option value="1" label="系统管理员"></el-option>
              </el-select>
            </el-form-item>
          </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="sumbitForm()">确定</el-button>
        </span>
      </el-dialog>

    </div>
    </div>
  </div>
</template>

<script>
import Headers from "./headers";
import Axios from "axios";
import { store } from "../vuex/store.js";
import Element from "element-ui"
import "element-ui/lib/theme-chalk/index.css"
// import Chat from './chat'
import Vue from 'vue';
Vue.use(Element);
export default {
  store,
  name: "news",
  data() {
    return {
      search_input: "",
      pageStart: 1,
      totles: 1,
      isEdit:false,
      tableData: [],
      currentPage1:1,
      turenamereq:'',
      turenameres:'',
      dialogVisible:false,
      form:{}

    };
  },
  methods: {
    getAdminList() {
      var _this = this;
      Axios({
        url: "/VueHandler/AdminHandler?action=show",
        method: "get",
        params: {
          pageStart: _this.pageStart,
          searchText: _this.search_input
        }
      }).then(function(res) {
        if (res.data.success) {
          _this.tableData = res.data.data.list;
          _this.totles = res.data.data.count;
          // console.log(_this.tableData)
        } else {
          _this.$message("没有数据");
        }
      });
    },
    //分页查询
			pageStarts(val) {
				//console.log(`当前页: ${val}`);
				this.pageStart = val
				this.getAdminList(this.pageStart)
			},
    search() {
      this.getAdminList();
    },
    add() {
      this.dialogVisible=true
      this.isEdit = false
      this.form={
        power:'1',
        phone:'',
        password:''
      }
    },
    editAdmins(row){
      this.isEdit = true
      console.log(row);
      this.dialogVisible = true
      this.form = Object.assign({},this.form,{
        tokenId : row.tokenId,
        turename : row.turename,
        password : row.password,
        phone : row.phone,
        power : '1',
      })

    },
    delAdmin(row){
      let _this = this
      Axios({
        url:'/VueHandler/AdminHandler?action=delete',
        method:'get',
        params:{
          tokenId:row.tokenId
        }
      }).then(function(res){
        // console.log(tableData.tokenId)
        if(res.data.success){
        _this.$message("删除成功")
        _this.getAdminList();        
        }else{
          _this.$message("删除失败")
        }
      })
    },
    // chatShow(item){
    //   this.$refs.headcom.getmyInfos()
    //   // this._idres=item._id;
    //   this.turenameres=item.turename;
    //   // this.$children[3].scrollBootom()
    //   // this.$refs.chatchild.getChatList()
    //   this.$refs.chatchild.scrollBootom()
      
    //   // console.log(this._idres,this.turenameres)
    // },
    userandid(user){
      this.turenamereq=user[0],
      this._idreq=user[1]
      // console.log(this._idreq,this.turenamereq)
    },
    sumbitForm() {
      var _this = this;
      if(this.form.tokenId){
        Axios({
        url: "/VueHandler/AdminHandler?action=update",
        method: "post",
        data: {
          tokenId: this.form.tokenId,
          turename: this.form.turename,
          phone: this.form.phone,
          password: this.form.password,
          powerCode: this.form.power
        }
      }).then(function(res) {
        if (res.data.success) {
            _this.getAdminList();
            _this.$message("修改成功");
            _this.dialogVisible = false
        } else {
            _this.$message("修改失败");
        }
      });
      }else{
        Axios({
        url: "/VueHandler/AdminLoginAndRegHandler?action=add",
        method: "post",
        data: {
          userName: this.form.userName,
          turename: this.form.turename,
          phone: this.form.phone,
          password: this.form.password,
          powerCode: this.form.power
        }
      }).then(function(res) {
        if (res.data.success) {
            _this.getAdminList();
            _this.$message("添加成功");
            _this.dialogVisible = false
        } else {
            _this.$message("添加失败");
        }
      });
      }
      
    },
  },
  components: { Headers },
  computed: {
    studentList() {
      return this.$store.state.studentList;
    }
  },
  mounted() {
    this.getAdminList();
    // this.$refs.chatchild.getChatNum()
  },

};
</script>

<style scoped>
</style>



