<template>
	<div class="login">
	<div class="container">
      <form class="form-signin">
        <div class="logo_img">
          <img src="../assets/images/logo.png" alt="">
        </div>
        <h2 class="form-signin-heading">宿舍管理系统54</h2><br>
        <label for="inputEmail" class="sr-only">用户名</label>
        <input type="username" id="inputUserName" ref="username" class="form-control" placeholder="请输入用户名" required autofocus><br>
        <label for="inputPassword" class="sr-only">密码</label>
        <input type="password" id="inputPassword" ref="password" class="form-control" placeholder="请输入密码" required>
           <br>
        <div class="checkbox"><br>
          <label class="float_left">
            <!-- <input type="checkbox" value="remember-me"> 记住用户名和密码 -->
          </label>
		  <router-link to="/register">
		  <!-- <p class="btn btn-normal float_right">注册</p>		   -->
		  </router-link>
        </div>
      </form>
        <button class="btn btn-lg btn-primary btn-block" @click="login()">登陆</button>
		    <button class="btn btn-lg btn-default btn-block" type="resize">重置</button>
    </div>
	</div>
	
</template>
<script>
import Axios from "axios";
import Code from "./code.vue";
import storage from "../assets/js/storage"
export default {
  name: "login",
  data() {
    return {};
  },
  mounted() {

  },
  components: {
    Code
  },
  methods: {
    login() {
      var _this = this;
        Axios({
          url: "/VueHandler/AdminLoginAndRegHandler?action=login",
          method: "post",
          data: {
            userName: _this.$refs.username.value,
            password: _this.$refs.password.value
          }
        }).then(function(res) {
          if (res.data.success) {
            storage.setSession("userInfo", res.data.userInfo)
            _this.$message.success('登录成功');            
            window.location.hash = "/home";
          } else {
            _this.$message("用户名或密码错误");
          }
        });
    }
  }
};
</script>
<style>
  .form-signin-heading{
    text-align: center;
  }
  .logo_img{
        text-align: center;
    margin-bottom: 20px;
  }
  .logo_img img{
        width: 130px;
    height: 130px;
  }
</style>





